@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
/** @format */
:root {
	--white: hsl(0, 0%, 100%);
}
nav.dark-mode {
	background-color: hsl(208, 29%, 23%);
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}
nav div {
	width: 100px;
	color: hsl(0, 0%, 100%);
	color: var(--white);
	padding-right: 2rem;
	cursor: pointer;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 0.9rem;
	font-weight: 600;
}
div {
	font-size: 14px;
}
h1.dark-mode {
	color: hsl(0, 0%, 100%);
	color: var(--white);
	margin: 0 0;
	padding: 0.8rem 2rem;
	font-size: 1.1rem;
}

/** @format */
:root {
	--background: hsl(207, 26%, 17%);
}
body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	font-family: 'Nunito Sans', sans-serif;
}
.header {
	width: 100%;
	height: 50px;
}
section {
	width: 100%;
	height: calc(100% - 0);
	background-color: hsl(207, 26%, 17%);
	background-color: var(--background);
}
section div {
	width: 85%;
	margin: auto;
	padding-top: 2rem;
}

/** @format */
:root {
	--white: hsl(0, 0%, 100%);
	--dark: hsl(208, 29%, 23%);
}
div.search-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	width: 290px;
	padding: 0.5rem;
	background-color: hsl(208, 29%, 23%);
	background-color: var(--dark);
	border-radius: 5px;
	margin-left: 0;
}
.search-container .search-icon {
	color: hsl(0, 0%, 100%);
	color: var(--white);
	font-size: 1.5rem;
}
.search-container input {
	width: 230px;
	border: none;
	background: none;
	color: hsl(0, 0%, 100%);
	color: var(--white);
	outline: none;
	font-size: 0.9rem;
}
.search-container input::-webkit-input-placeholder {
	color: hsl(0, 0%, 100%);
	color: var(--white);
}
.search-container input:-ms-input-placeholder {
	color: hsl(0, 0%, 100%);
	color: var(--white);
}
.search-container input::placeholder {
	color: hsl(0, 0%, 100%);
	color: var(--white);
}

/** @format */
:root {
	--dark: hsl(208, 29%, 23%);
	--white: hsl(0, 0%, 100%);
}
ul {
	margin-left: auto;
	padding: 0;
	list-style: none;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	grid-gap: 2rem;
	gap: 2rem;
}
ul li {
	width: 200px;
	height: 280px;
	background-color: hsl(208, 29%, 23%);
	background-color: var(--dark);
	border-radius: 5px;
	color: hsl(0, 0%, 100%);
	color: var(--white);
	cursor: pointer;
}
img {
	width: 100%;
	height: 50%;
}
h1 {
	--white: hsl(0, 0%, 100%);
	font-size: 1rem;
}
p {
	font-size: 14px;
	margin: 0;
}
h1,
p {
	margin-left: 1rem;
	text-align: left;
}
span {
	font-weight: 700;
}

/** @format */

section article img {
	width: 100px;
}

